*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif;
  
  
}
.contains{
  height: 100vh;
  text-align: center;
  padding-top: 6em;
  
}
h1{
  font-size: 3em;
  color: #000080;
  
}
p{
  font-size: 4em;
  font-weight: bold;
  padding: 20px;
}
.incre, .decre, .reset{
  margin: 10px;
  
}
.incre{
  border: none;
  background-color: #00b300;
  border-radius: 20px; 
}
.decre{
  border: none;
  border-radius: 20px;
  background-color: #ff3333;
}
.reset{
  background-color: #ffd11a;
  border: none;
  
}
  

span{
  font-size: 55px;
  padding: 10px;
  
}
.res{
  font-size: 30px;
  padding: 10px;
  background-color: #ffd11a;
  border-radius: 10px;
  
}
.dec{
  padding-left: 18px;
  padding-right: 18px;
}

